          {pic: ['photo/P139_1.jpg','photo/P139_2.jpg'],no: 'AT0028',position: [112.942089,28.044937],feeStandard:'计次收费，5元/次/12小时',name:'湘江南路新港村口停车场',addr:'大托铺街道新港村',type:'地面',area:'3500㎡',num:'59个',company:'长沙城投机场迁建投资开发有限责任公司',mode:'闲置',isSys:'否'},
          {pic: ['photo/P140_1.jpg','photo/P140_2.jpg'],no: 'AT0029',position: [112.934477,28.059551],feeStandard:'计次收费，5元/次/12小时',name:'南部片区规划展示区停车场',addr:'湘江南路天心区交警大楼西',type:'临时地面',area:'2232㎡',num:'74个',company:'长沙城投机场迁建投资开发有限责任公司',mode:'闲置',isSys:'否'},
          {pic: ['photo/P141_1.jpg','photo/P141_2.jpg'],no: '审批中',position: [112.971529,28.165488],feeStandard:'计次收费，10元/次/12小时',name:'蓝湾国际广场停车场',addr:'南湖路与书院路交汇入',type:'地下',area:'2450㎡',num:'70个',company:'湖南鹏鑫投资置业有限公司',mode:'自营',isSys:'否'},
          {pic: ['photo/P142_1.jpg','photo/P142_2.jpg'],no: '审批中',position: [112.961849,28.104951],feeStandard:'计次收费，10元/次/12小时',name:'肉联厂小区公共停车场',addr:'天心区新开铺路肉联厂小区西门口',type:'临时地面',area:'2500㎡',num:'75个',company:'长沙市南湖新城建设开发有限责任公司',mode:'闲置',isSys:'否'}
        ]
        for (let i = 0; i < info.length; i++) {
          if(info[i].type=='地下'){
            that.icon=iconPic2
          }else if(info[i].type=='生态地面'){
            that.icon=iconPic3
          }else{
            that.icon=iconPic
          }

          that.markers.push({
            position: info[i].position,
            icon:that.icon,
            events: {
              click: (e) => {
                that.windows.forEach(window => {
                  window.visible = false
                })
                that.$nextTick(() => {
                  that.currentWindow = that.windows[i];
                  that.currentWindow.visible = true;
                })
                setTimeout(() => {
                  const gallery = new Viewer(document.querySelector('.image-gallery'), {
                    transition: true,
                    toolbar: {
                      prev: 1,
                      zoomIn:1,
                      zoomOut:1,
                      next: 1
                    }
                  })
                }, 50)  // 等待DOM渲染
              }
            }
          })
          console.log('ffff')
          let title=info[i].name
          let content='<table>'
                      + '<tr class="image-gallery"><td style="width:50%; text-align:center;">'
                      + '  <img src="/' + info[i].pic[0] + '" border="0" class="info-image" style="width:120px;height: 80px;padding-right: 5px;">'
                      + '</td>'
                      + '<td style="width:50%; text-align:center;">'
                      + '  <img src="/' + info[i].pic[1] + '" border="0" class="info-image" style="width:120px;height: 80px;">'
                      +' </td></tr>'
                      + '<tr><td colspan="2"><strong>备案号：</strong>' + info[i].no + '</td></tr>'
                      + '<tr><td colspan="2"><strong>地址：</strong>' + info[i].addr + '</td></tr>'
                      + '<tr><td colspan="2"><strong>类型：</strong>' + info[i].type + '</td></tr>'
                      + '<tr><td colspan="2"><strong>面积：</strong>' + info[i].area + '</td></tr>'
                      + '<tr><td colspan="2"><strong>车位数量：</strong>' + info[i].num + '</td></tr>'
                      + '<tr><td colspan="2"><strong>经营方式：</strong>' + info[i].mode + '</td></tr>'
                      + '<tr><td colspan="2"><strong>收费标准：</strong>' + info[i].feeStandard + '</td></tr>'
                      + '<tr><td colspan="2"><strong>是否安装道闸收费系统：</strong>' + info[i].isSys + '</td></tr>'
                      + '<tr><td colspan="2"><strong>权属单位：</strong>' + info[i].company + '</td></tr>'
                      + '</table>'
          that.windows.push({
            position: info[i].position,
            isCustom: true,
            content: this.createInfoWindow(title,content),
            offset: [15, -35],
            visible: false
          })
        }
      },
      //构建自定义信息窗体
      createInfoWindow: function (title, content) {
        var info = document.createElement("div");
        info.className = "custom-info input-card content-window-card";

        //可以通过下面的方式修改自定义窗体的宽高
        info.style.width = "300px";
        // 定义顶部标题
        var top = document.createElement("div");
        var titleD = document.createElement("div");
        var closeX = document.createElement("img");
        top.className = "info-top";
        titleD.innerHTML = title;
        closeX.src = "https://webapi.amap.com/images/close2.gif";
        closeX.onclick = this.closeInfoWindow;

        top.appendChild(titleD);
        top.appendChild(closeX);
        info.appendChild(top);

        // 定义中部内容
        var middle = document.createElement("div");
        middle.className = "info-middle";
        middle.style.backgroundColor = '#CDE8FF';
        middle.innerHTML = content;
        info.appendChild(middle);

        // 定义底部内容
        var bottom = document.createElement("div");
        bottom.className = "info-bottom";
        bottom.style.position = 'relative';
        bottom.style.top = '-1px';
        bottom.style.margin = '0 auto';
        var sharp = document.createElement("img");
        sharp.src = require("../../assets/sharp.png");
        bottom.appendChild(sharp);
        info.appendChild(bottom);
        return info;
      },
      //关闭信息窗体
      closeInfoWindow: function () {
        let map = this.amapManager.getMap()
        map.clearInfoWindow();
      },
      showChart(){

        let myBar = this.$echarts.init(this.$refs.myBar)
        let dataAxis = ['自营','租赁','合作','其它'];
        let data = [85,37,1,19];
        let yMax = 100;
        let dataShadow = [];

        for (var i = 0; i < data.length; i++) {
          dataShadow.push(yMax);
        }
        // 绘制图表
        myBar.setOption({

          title : {
            text: '运营模式',
            x: 'center',
            y: 'bottom',
            textStyle: {//主标题样式
              fontSize: 14,
              fontWeight: 'bold',
              color:'#53E1EB'
            }
          },
          grid:{
            show:false,
            left:'60px',
            right:'40px'
          },
          xAxis: {
            data: dataAxis,
            axisLabel: {
              show:false,
              inside: true,
              textStyle: {
                color: '#fff',
                fontSize:'10px'
              }
            },
            axisTick: {
              show: true
            },
            axisLine: {
              show: false
            },
            z: 10
          },
          yAxis: {
            axisLine: {
              show: false
            },
            axisTick: {
              show: true
            },
            axisLabel: {
              textStyle: {
                color: '#999'
              }
            }
          },
          dataZoom: [
            {
              type: 'inside'
            }
          ],
          series: [
            { // For shadow
              type: 'bar',
              itemStyle: {
                normal:{
                  color: 'rgba(83,225,235,0.1)'
                },
              },
              barGap: '-100%',
              barCategoryGap: '40%',
              data: dataShadow,
              animation: false
            },
            {
              type: 'bar',
              data: data,
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                },
                normal:{
                  color:function(params) {
                    //自定义颜色
                    var colorList = [
                      '#27727B','#FE8463','#9BCA63','#FAD860','#F3A43B','#60C0DD','#D7504B','#C6E579','#F4E001','#F0805A','#26C0C0','#FF9F7F','#FB7293','#E690D1','#E7BCF3','#9D96F5','#8378EA','#96BFFF'
                    ];
                    return colorList[params.dataIndex]
                  },
                  label:{
                    show:true,
                    position:'insideBottom',
                    formatter:function(d){
                      let lbl=dataAxis[d.dataIndex].split('')
                      return lbl.join('\n')
                    },
                    textStyle:{fontSize:12}
                  }
                }
              }
            }
          ]
        })


        let myPie=this.$echarts.init(this.$refs.myPie)
        myPie.setOption({
          title : {
            text: '管理方式',
            x: 'center',
            y: 'bottom',
            textStyle: {//主标题样式
              fontSize: 14,
              fontWeight: 'bold',
              color:'#53E1EB'
            }
          },
          label: {
            show: false,
            position: 'center'
          },
          legend: {
            orient: 'horizontal',
            x: 'center',
            y: 'top',
            top:0,
            textStyle:{
              color:'#fff'
            },
            data: ['无人值守', '有人值守', '人工收费']
          },
          series: [
            {
              type: 'pie',
              radius: '50%',
              center: ['50%', '50%'],
              selectedMode: 'single',
              label:{            //饼图图形上的文本标签
                normal:{
                  show:true,
                  position:'inner', //标签的位置
                  textStyle : {
                    fontWeight : 300 ,
                    fontSize : 10    //文字的字体大小
                  },
                  formatter:'{d}%'
                }
              },
              data: [
                {value: 48,name: '无人值守'},
                {value: 28, name: '有人值守'},
                {value: 9, name: '人工收费'}
                ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                },
                normal:{
                  color:function(params) {
                    //自定义颜色
                    var colorList = [
                      '#96C97D', '#FDC65C', '#67E0E3'
                    ];
                    return colorList[params.dataIndex]
                  }
                }
              }
            }
          ]
        })
      }
    },
    watch: {

    },
    components: {
      CountTo
    }

  }
</script>
<style >
  /* 针对WebKit内核（Chrome、Safari） */
  ::-webkit-scrollbar {
    width: 8px; /* 设置滚动条宽度为8像素 */
  }

  ::-webkit-scrollbar-track {
    background-color: #f1f1f1; /* 设置背景色为灰色 */
  }

  ::-webkit-scrollbar-thumb {
    border-radius: 4px; /* 设置拖动按钮边角为4像素 */
    background-color: #ccc; /* 设置拖动按钮背景色为灰色 */
  }

  ::-webkit-scrollbar-thumb:hover {
    background-color: #999; /* 当鼠标悬停在滚动条上时改变拖动按钮背景色为深灰色 */
  }
  body{
    background: url("../../assets/bg.png") no-repeat center center;
    background-size: cover;
  }
  .app-container{
    min-height: 800px;
    margin: 0;
    padding: 0;
  }
  .top {
    width: 100%;
    margin: 0 auto;
    background: url("../../assets/title.png") no-repeat center 0;
    background-size: contain;
    height: 80px;
  }
  .title {
    height: 60px;
  }
  .title p{
    text-align: center;
    color:#53E1EB;
    font-size: 22px;
    line-height: 40px;
    vertical-align: middle;
    font-weight: bold;
    margin: 0;
  }
  .main{
    width: 100%;
    height: 700px;
    margin: 0 auto;
  }
  .company-position {
    width: 300px;
    height: 700px;
    background: url("../../assets/company-position-bg.png") no-repeat center center;
    background-size: cover;
    margin-bottom: 40px;
  }
  .position-title {
    width: 100%;
    height: 30px;
    background: url("../../assets/position-title-bg.png") no-repeat center center;
  }
  .position-title p{
    color: #53E1EB;
    font-size: 16px;
    margin: 0;
    margin-top: 5px;
    text-align: center;
    font-weight: bolder;
  }
  .amap-wrapper{
    width: 100%;
    height: 100%;
  }

  .content-window-card p {
    height: 2rem;
  }


  div.info-top {
    position: relative;
    background: none repeat scroll 0 0 #4B6F9E;
    border-bottom: 1px solid #3A557A;
    border-radius: 5px 5px 0 0;
  }

  div.info-top div {
    display: inline-block;
    color: #fff;
    font-size: 14px;
    font-weight: bold;
    line-height: 31px;
    padding: 0 10px;
    width: 100%;
    text-align: left;
  }

  div.info-top img {
    position: absolute;
    top: 10px;
    right: 10px;
    transition-duration: 0.25s;
  }

  div.info-top img:hover {
    box-shadow: 0px 0px 5px #fff;
  }

  div.info-middle {
    font-size: 12px;
    padding: 10px 6px;
    line-height: 20px;
    text-align: left;
    border: 1px solid #3A557A;
  }

  div.info-bottom {
    height: 0px;
    width: 100%;
    clear: both;
    text-align: center;
  }

  div.info-bottom img {
    position: relative;
    z-index: 104;
  }

  span {
    margin-left: 5px;
    font-size: 11px;
  }

  .info-middle img {
    float: left;
    margin-right: 6px;
  }

  .map-class{
    background: url('../../assets/map-bg.png') no-repeat center center;
    background-size: cover;
  }

</style>



